import { useState, useEffect, useRef } from 'react';

function Marker(options) {

    const marker = useRef()

    useEffect(() => {
        if (!marker.current) {
            marker.current = new window.google.maps.Marker();
        }
        return () => {
            if (marker.current) {
                marker.current.setMap(null);
            }
        };
    }, [])

    useEffect(() => {
        if (marker.current) {
            marker.current.setOptions(options);
        }

    }, [options])

    return null
}


export default Marker;