import './App.scss';
import { Wrapper } from '@googlemaps/react-wrapper'
import MainMap from './Components/MainMap/MainMap';
import { INIT_CENTER, INIT_ZOOM, MAP_API_KEY } from './Shared/Configs';
import React, { useEffect, useRef, useState } from 'react'
import Grid from '@mui/material/Grid';
import { Store } from './Shared/Context';
import TextField from '@mui/material/TextField';
import Container from '@mui/material/Container'
import NotAuthPage from './Components/NotAuthPage/NotAuthPage';
import { formatCenter, drawShape, validateAccessToken, formatInfoContent, setPolygonEvents } from './Shared/Utils';
import Marker from './Components/Marker/Marker';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Button from '@mui/material/Button';

function App() {

  const [zoom, setZoom] = useState(INIT_ZOOM);
  const [center, setCenter] = useState(INIT_CENTER);
  const [map, setMap] = useState('');
  const [markers, setMarkers] = useState([]);
  const [mapTypeId, setMapTypeId] = useState('roadmap');
  const centerBoundsRef = useRef('');
  const [enableFetching, setEnableFetching] = useState(true);
  const [enableManualFetch, setEnableManualFetch] = useState(false)
  const [transactions, setTransactions] = useState([]);
  const [infoWindow, setInfoWindow] = useState();
  const [isWidget, setIsWidget] = useState(false)

  //used to check if auth to embedded app as widget
  const [notAuthToAccess, setNotAuthToAccess] = useState(false);
  const [, forceUpdate] = useState({});
  const [disableDefMapUI, setDisableDefMapUI] = useState(false);
  const polygons = useRef([]);
  const centerOfPolygons = useRef([])
  const priceAve = useRef();

  const contextValue = {
    onIdle,
    zoom,
    center,
    notAuthToAccess,
    onClick,
    mapTypeId,
    map,
    setMap,
    isWidget,
    disableDefaultUI: disableDefMapUI,
  }
  //////////////////////////////////////////////////////////

  useEffect(() => {
    //Iframe mode
    if (window.top !== window.self) {
      // for development purpose 
      // window.__REACT_DEVTOOLS_GLOBAL_HOOK__ = window.parent.__REACT_DEVTOOLS_GLOBAL_HOOK__;
      //check token
      setIsWidget(true);
      const params = new URLSearchParams(window.location.search);
      const accessToken = params.get('AccessToken');
      const isValid = validateAccessToken(accessToken)

      if (!isValid) {
        setNotAuthToAccess(true);

      } else {

        window.addEventListener("message", onActionReceivedFromHost)
        setDisableDefMapUI(true)

      }

      return () => {
        window.removeEventListener("message", onActionReceivedFromHost);
      }

    }
  }, [map])

  useEffect(() => {
    forceUpdate({})
  }, [markers])

  //////////////////////////////////////////////////////////////////////////////////



  function createMarker(place) {
    const position = {
      lng: place?.geometry?.location?.lng(),
      lat: place?.geometry?.location?.lat()
    }
    setMarkers(state => {
      console.log(state)
      return [{ position }]

    })
  }

  function placeSearch(e) {
    const searchTxt = e.data?.data?.payload;

    const request = {
      query: searchTxt,
      fields: ['name', "geometry"],
      locationBias: centerBoundsRef.current
    }

    const service = new window.google.maps.places.PlacesService(map);
    service.findPlaceFromQuery(request, (results, status) => {
      if (status === window.google.maps.places.PlacesServiceStatus.OK && results) {
        results.forEach(place => {
          createMarker(place)
        })
      }
    })
  }

  function changeMapType(e) {
    const mapType = e.data?.data?.payload;
    setMapTypeId(mapType);

  }

  function onActionReceivedFromHost(e) {
    if (notAuthToAccess) {
      return;
    }

    if (e.data?.type === 'sari-host') {
      if (e.data?.data?.action === 'place-search') {
        placeSearch(e)
      }

      if (e.data?.data?.action === 'switch-fetching') {
        const isFetchingEnabled = e.data?.data?.payload;
        setEnableFetching(isFetchingEnabled);
      }

      if (e.data?.data?.action === 'set-default') {
        const _zoom = e.data?.data?.payload?.zoomLevel;
        const _center = e.data?.data?.payload?.center;
        if (_zoom) {
          setZoom(parseInt(_zoom));
        }
        if (_center) {
          const formattedCenter = formatCenter(_center)
          if (formattedCenter) {
            setCenter(formattedCenter)
          }
        }
      }

      if (e.data?.data?.action === 'map-type') {
        changeMapType(e)

      }

    }
  }



  function onClick(e, m) {
    // polygons.current.forEach((polygon) => {
    //   window.google.maps.event.clearListeners(polygon, 'mouseover')
    //   window.google.maps.event.clearListeners(polygon, 'mouseout')
    //   polygon.setMap(null)

    // })


  }

  function onIdle(m) {
    console.log('onIdle');
    const currZoom = m.getZoom();
    const currCenter = m.getCenter().toJSON();
    setInfoWindow(new window.google.maps.InfoWindow);
    if (enableFetching && !enableManualFetch && currZoom >= 14) {
      fetchingData(m)
        .then((resp) => {
          if (resp) {
            setTransactions(resp.data);
            const { _centerOfPolygons, _priceAve } = drawShape(resp.data, map, polygons, infoWindow)
            centerOfPolygons.current = _centerOfPolygons;
            priceAve.current = _priceAve;
            setPolygonEvents(polygons, infoWindow, resp.data, centerOfPolygons.current, map, currZoom)
          }

        })

    } else if (transactions.length) {
      setPolygonEvents(polygons, infoWindow, transactions, centerOfPolygons.current, map, currZoom, priceAve.current)
    }

    // if (transactions.length && zoom < 14) {

    //   setPolygonEvents(polygons, infoWindow, transactions, centerOfPolygons.current, map, zoom, priceAve.current)

    // }
    setCenter(currCenter)
    setZoom(currZoom);

  }

  async function fetchingData(m) {
    const centerBounds = m.getBounds();
    centerBoundsRef.current = centerBounds;
    const NE = centerBounds.getNorthEast();
    const SW = centerBounds.getSouthWest();
    const zoom = m.getZoom();

    const resp = await fetch(`https://api.suhail.ai/transactions/boundaries?boundaryFrom=${SW.lng()},${NE.lng()}&boundaryTo=${SW.lat()},${NE.lat()}&period=one_month`,
      {
        method: "GET",
      }
    )
    return await resp.json()

  }

  function onFetchingModeChanged(e) {
    setEnableManualFetch(e.target.value === 'manual')
    // fetchingData(map)
  }

  function onDataFetchManually() {
    if (zoom >= 14) {

      fetchingData(map)
        .then(resp => {
          if (resp) {
            setTransactions(resp.data);
            const { _centerOfPolygons, _priceAve } = drawShape(resp.data, map, polygons, infoWindow)
            centerOfPolygons.current = _centerOfPolygons;
            priceAve.current = _priceAve;
            setPolygonEvents(polygons, infoWindow, resp.data, centerOfPolygons.current, map, zoom)
          }
        })
    }
  }

  ////////////////////////////////////////

  if (notAuthToAccess) {
    return <NotAuthPage />
  }

  return (
    <Store.Provider value={contextValue}>
      <div className='margin-25'>
        <Grid container spacing={3}>
          <Grid item sm={isWidget ? 12 : 8} lg={isWidget ? 12 : 8} md={isWidget ? 12 : 8}>
            <Wrapper apiKey={MAP_API_KEY} libraries={["places"]} render={(status) => {
              return <h1>{status}</h1>
            }}>
              <MainMap center={center} zoom={zoom}>
                {markers.map((marker, idx) => {
                  return <Marker position={marker.position} key={idx} />
                })}
              </MainMap>
            </Wrapper>
          </Grid>

          <Grid item md={12} lg={12}>

            <Grid container rowSpacing={3}>
              <Grid item md={12} sm={12}>
                <TextField
                  disabled
                  label="Zoom Level"
                  value={zoom}
                  sx={{ pt: 0.25 }}
                  size={'small'}
                />

              </Grid>
              <Grid item md={12} sm={12}>
                <Grid container spacing={3}>
                  <Grid item md={6} lg={6}>
                    <FormControl fullWidth >
                      <InputLabel
                        size="small"
                        id="fetch-mode"
                        title='Fetching Data Mode'
                      >
                        Fetching Data Mode
                      </InputLabel>
                      <Select
                        labelId="fetch-mode"
                        label="Fetching Data Mode"
                        size="small"
                        onChange={onFetchingModeChanged}
                        defaultValue="auto"
                        disabled={!enableFetching}
                      >
                        <MenuItem value={"auto"}>Auto</MenuItem>
                        <MenuItem value={"manual"}>Manual</MenuItem>
                      </Select>
                    </FormControl>

                  </Grid>

                  {enableManualFetch ?
                    <Grid item md={6} lg={6}>

                      <Button
                        size={'medium'}
                        variant={'contained'}
                        color={"success"}
                        onClick={onDataFetchManually}
                        disabled={!enableFetching}
                      >
                        <div title='Fetch Data' className='text-ellipsis '>
                          Fetch Data
                        </div>
                      </Button>
                    </Grid> : null}
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

      </div>


    </Store.Provider >


  );
}

export default App;




