import { createCustomEqual } from 'fast-equals';
import { ACCESS_TOKENS } from './Configs';
import React from 'react';
import isNumber from 'lodash/isNumber';

const deepCompareEqualsForMaps = createCustomEqual(
    (deepEqual) => (a, b) => {
        if (
            // isLatLngLiteral(a) ||
            a instanceof window.google.maps.LatLng ||
            // isLatLngLiteral(b) ||
            b instanceof window.google.maps.LatLng
        ) {
            return new window.google.maps.LatLng(a).equals(new window.google.maps.LatLng(b));
        }
        return deepEqual(a, b);
    }
);

function useDeepCompareMemoize(value) {
    const ref = React.useRef();

    if (!deepCompareEqualsForMaps(value, ref.current)) {
        ref.current = value;
    }

    return ref.current;
}

export function useDeepCompareEffectForMaps(callback, dependencies) {
    React.useEffect(callback, dependencies.map(useDeepCompareMemoize));
}

export function validateAccessToken(token) {
    return ACCESS_TOKENS.includes(token)
}


export function createMarker(place) {
    return {
        lng: place?.geometry?.location?.lng(),
        lat: place?.geometry?.location?.lat()
    }

}

export function placeSearch(e, centerBounds, map) {
    const searchTxt = e.data?.data?.payload;

    const request = {
        query: searchTxt,
        fields: ['name', "geometry"],
        locationBias: centerBounds
    }

    const service = new window.google.maps.places.PlacesService(map);
    service.findPlaceFromQuery(request, (results, status) => {
        if (status === window.google.maps.places.PlacesServiceStatus.OK && results) {
            return results
        } else {
            return [];
        }
    })
}

export function onActionReceivedFromHost(e, centerBounds, map, setMarkers) {

    if (e.data?.type === 'sari-host') {
        if (e.data?.data?.action === 'place-search') {
            const places = placeSearch(e, centerBounds, map)
            places.forEach(place => {
                const position = createMarker(place);
                setMarkers(state => [...state, { position }])
            })
        }
    }
}
const useForceUpdate = () => {
    const [, setState] = React.useState();
    return () => setState({});
};

