export function formatCenter(strCenter) {
    const arrCenter = strCenter.split(',');
    const lng = parseFloat(arrCenter[0]);
    const lat = parseFloat(arrCenter[1]);
    if (isNumber(lng) && isNumber(lat)) {
        return {
            lng, lat
        }
    }
    else {
        return {}
    }


}

export function drawShape(transactions, map, polygons, infoWindow) {

    polygons.current.forEach((polygon) => {
        infoWindow.close();
        window.google.maps.event.clearListeners(polygon, 'mouseover')
        window.google.maps.event.clearListeners(polygon, 'mouseout')
        polygon.setMap(null)

    })
    const sharedOpts = {
        strokeColor: "#FF0000",
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: "#FF0000",
        fillOpacity: 0.35,
    }


    const _centerOfPolygons = []
    let priceSum = 0

    transactions.map((trans, idx) => {
        let coordinates = JSON.parse(trans?.polygonData)?.coordinates;
        _centerOfPolygons.push({ lng: trans.centroidX, lat: trans.centroidY })
        const formattedCoord = coordinates[0].map(point => {
            return {
                lng: point[0],
                lat: point[1]
            }
        })
        polygons.current[idx] = new window.google.maps.Polygon(
            {
                path: formattedCoord,
                ...sharedOpts
            }
        )
        polygons.current[idx].setMap(map);

        priceSum += trans.transactionPrice

    })

    const _priceAve = priceSum / transactions.length;

    return { _centerOfPolygons, _priceAve };

}


export function setPolygonEvents(polygons, infoWindow, transactions, centerOfPolygons, map, zoom, priceAve) {


    polygons.current.forEach((polygon) => {
        window.google.maps.event.clearListeners(polygon, 'mouseover')
        window.google.maps.event.clearListeners(polygon, 'mouseout')
        infoWindow.close();

    })

    // const priceAverage = parseFloat(priceSum) / transactions.length;

    polygons.current.forEach((polygon, idx) => {
        polygon.addListener('mouseover', (e) => {
            infoWindow.setPosition(centerOfPolygons[idx])
            infoWindow.setContent(formatInfoContent(transactions[idx], zoom, priceAve))
            infoWindow.open(map);
        })
        polygon.addListener('mouseout', () => {

            infoWindow.close();
        })
    })
}

export function formatInfoContent(tran, zoom, priceAve) {

    if (zoom < 14) {
        return (`
            <div>
                <strong>Average Price:</strong> &nbsp; <span>${priceAve}</span>
            </div>`
        )
    }

    return (`
        <div>
            <div>
                <strong>transaction No:</strong>&nbsp;<span>${tran.transactionNumber}</span>

            </div>
            <div>
                <strong>Type:</strong>&nbsp;<span>${tran.type}</span>

            </div>
            <div>
                <strong>Metric Type:</strong>&nbsp;<span>${tran.metricsType}</span>

            </div>
            <div>
                <strong>transaction Price:</strong>&nbsp;<span>${tran.transactionPrice}</span>

            </div>
            <div>
                <strong>transaction Price of Meter:</strong>&nbsp;<span>${tran.priceOfMeter}</span>

            </div>
            <div>
                <strong>transaction Date:</strong>&nbsp;<span>${tran.transactionDate}</span>

            </div>
            <div>
                <strong>Sub Division No.:</strong>&nbsp;<span>${tran.subdivisionNo}</span>

            </div>
            <div>
                <strong>No. of Properties:</strong>&nbsp;<span>${tran.noOfProperties}</span>

            </div>
            <div>
                <strong>Zoning Id:</strong>&nbsp;<span>${tran.zoningId || 'N/A'}</span>

            </div>
        </div>
    `)
}