import React, { useContext, useEffect, useRef, useState } from 'react'
import { Store } from '../../Shared/Context';
import "./_main-map.scss";
import { useDeepCompareEffectForMaps } from '../../Shared/Utils';
const MainMap = ({ children }) => {

    const mapRef = useRef();
    const context = useContext(Store)
    const { notAuthToAccess, onClick, onIdle, map, setMap, polygon, isWidget, setPolygon, ...options } = context;
    useEffect(() => {

        if (mapRef.current && !map) {
            setMap(new window.google.maps.Map(mapRef.current, { center: options.center, zoom: options.zoom }))
        }

        if (map) {
            ["idle", "click"].forEach((eventName) => {
                window.google.maps.event.clearListeners(map, eventName)
            });
            map.addListener('idle', () => onIdle(map))
            map.addListener('click', (e) => onClick(e, map))
        }
    }, [map, mapRef, onClick, onIdle])


    useDeepCompareEffectForMaps(() => {
        if (map) {
            map.setOptions(options);
        }
    }, [map, options]);

    return (
        <>
            <div ref={mapRef} className={isWidget ? "map-widget" : "map"} />
            {React.Children.map(children, child => {
                if (React.isValidElement(child)) {
                    return React.cloneElement(child, { map })
                }
            })}
        </>
    )
}

export default MainMap